import React, { useState, useEffect } from 'react';
import { Link } from 'gatsby';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
import PropTypes from 'prop-types';
import { Layout } from '@components';
import styled from 'styled-components';
import { theme, mixins, media, Main } from '@styles';
const { colors, fonts } = theme;

const StyledMainContainer = styled(Main)`
  ${mixins.flexCenter};
  flex-direction: column;
  overflow: hidden;
`;

const StyledImg = styled.img`
width: 30%;
${media.phablet`width: 80%;`}
${media.thone`width: 50%;`}
  ${media.bigDesktop`width: 50%;`}
  ${media.giant`width: 40%;`};
  &:hover,
  &:focus {
      transform: scale(1.1);
      transition: 2s
    }
  }
`;

const StyledDataContainer = styled.div`
  ${mixins.flexCenter};
  gap: 2rem;
  width: 80%;
  margin: 0 auto;
`;

const StyledTitle = styled.h1`
  color: ${colors.green};
  font-family: ${fonts.SFMono};
  font-size: 12vw;
  line-height: 1;
  ${media.bigDesktop`font-size: 200px;`}
  ${media.phablet`font-size: 120px;`};
`;
const StyledSubtitle = styled.h2`
  font-size: 3vw;
  font-weight: 400;
  ${media.bigDesktop`font-size: 50px;`};
  ${media.phablet`font-size: 30px;`};
`;
const StyledHomeButton = styled(Link)`
  ${mixins.bigButton};
  margin-top: 40px;
`;

const NotFoundPage = ({ location }) => {
  const [isMounted, setIsMounted] = useState(false);

  useEffect(() => {
    const timeout = setTimeout(() => setIsMounted(true), 1000);
    return () => clearTimeout(timeout);
  }, []);

  return (
    <Layout location={location}>
      <TransitionGroup component={null}>
        {isMounted && (
          <CSSTransition timeout={500} classNames="fade">
            <StyledMainContainer className="fillHeight">
              <StyledDataContainer>
                <div>
                  <StyledTitle>404</StyledTitle>
                  <StyledSubtitle>Page Not Found</StyledSubtitle>
                </div>
                <StyledImg src="https://www.salehriaz.com/404Page/img/astronaut.svg" />
              </StyledDataContainer>

              <StyledHomeButton to="/">Go Home</StyledHomeButton>
            </StyledMainContainer>
          </CSSTransition>
        )}
      </TransitionGroup>
    </Layout>
  );
};

NotFoundPage.propTypes = {
  location: PropTypes.object.isRequired,
};

export default NotFoundPage;
